___CSS_LOADER_EXPORT___.push([module.id, `*{
    box-sizing: border-box;
    line-height: 1.5;
}

body, h2{
    margin: 0;
}

.container{
    display: grid;
    grid-template: 530px 1fr/270px 1fr;
    border: 5px solid #02699c;
}

.top{
    border-left: 5px solid  #02699c;
    padding-left: 10px;
}

.top > form > div{
    display: flex;
}

.left{
    grid-row: 1/3;
    padding-top: 10px;
    padding-left: 7px;
    font-size: 1.2rem;
    background-color: #50B6F2;
    color: #ffffff;
}

.left > .container-nav-sidebar{
    margin-bottom: 20px;
}

.container-nav-sidebar label{
    font-size: 2rem;
}


.container-not-in-project,
.project{
    padding: 5px;
}

.container-not-in-project:hover,
.project:hover{
    background-color: #00273E;
}

.content{
    border-top: 10px solid #02699c;
    border-left: 5px solid #02699c;
    padding-top: 20px;
    padding-left: 10px;
    padding-bottom: 10px;
    padding-right: 10px;
}

.container-list{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.container-todo-list > button{
    margin-top: 5px;
    margin-right: 10px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,yBAAyB;AAC7B;;AAEA;IACI,+BAA+B;IAC/B,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,eAAe;AACnB;;;AAGA;;IAEI,YAAY;AAChB;;AAEA;;IAEI,yBAAyB;AAC7B;;AAEA;IACI,8BAA8B;IAC9B,8BAA8B;IAC9B,iBAAiB;IACjB,kBAAkB;IAClB,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB","sourcesContent":["*{\n    box-sizing: border-box;\n    line-height: 1.5;\n}\n\nbody, h2{\n    margin: 0;\n}\n\n.container{\n    display: grid;\n    grid-template: 530px 1fr/270px 1fr;\n    border: 5px solid #02699c;\n}\n\n.top{\n    border-left: 5px solid  #02699c;\n    padding-left: 10px;\n}\n\n.top > form > div{\n    display: flex;\n}\n\n.left{\n    grid-row: 1/3;\n    padding-top: 10px;\n    padding-left: 7px;\n    font-size: 1.2rem;\n    background-color: #50B6F2;\n    color: #ffffff;\n}\n\n.left > .container-nav-sidebar{\n    margin-bottom: 20px;\n}\n\n.container-nav-sidebar label{\n    font-size: 2rem;\n}\n\n\n.container-not-in-project,\n.project{\n    padding: 5px;\n}\n\n.container-not-in-project:hover,\n.project:hover{\n    background-color: #00273E;\n}\n\n.content{\n    border-top: 10px solid #02699c;\n    border-left: 5px solid #02699c;\n    padding-top: 20px;\n    padding-left: 10px;\n    padding-bottom: 10px;\n    padding-right: 10px;\n}\n\n.container-list{\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n}\n\n.container-todo-list > button{\n    margin-top: 5px;\n    margin-right: 10px;\n}"],"sourceRoot":""}]);
